<template><section><!--
 * @Author: Fone丶峰
 * @Date: 2019-10-22 13:23:50
 * @LastEditors: Fone丶峰
 * @LastEditTime: 2020-04-24 15:43:37
 * @Description: msg
 * @Email: qinrifeng@163.com
 * @Github: https://github.com/FoneQinrf
 -->
<h3>ActionSheet 上拉菜单</h3>
<hr>
<h4>使用方式</h4>
<pre v-pre><code v-pre class="language-javascript">
<span class="hljs-keyword">import</span> Vue <span class="hljs-keyword">from</span> <span class="hljs-string">'vue'</span>
<span class="hljs-keyword">import</span> { ActionSheet } <span class="hljs-keyword">from</span> <span class="hljs-string">'vvmui'</span>

Vue.component(ActionSheet)

</code></pre>
<h4>基本用法</h4>
<p>通过<code v-pre>data</code>设置菜单</p>
<h5>代码示例</h5>
<pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">ActionSheet</span> <span class="hljs-attr">:data</span>=<span class="hljs-string">"[{label:'选项一',value:1},{label:'选项二',value:2}]"</span> /&gt;</span>

</code></pre>
<h4>v-model使用</h4>
<h5>代码示例</h5>
<pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">ActionSheet</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value"</span> <span class="hljs-attr">:data</span>=<span class="hljs-string">"[{label:'男',value:1},{label:'女',value:2}]"</span> /&gt;</span>

</code></pre>
<h4>禁用</h4>
<p>通过<code v-pre>disabled</code>属性可禁用<code v-pre>ActionSheet</code></p>
<h5>代码示例</h5>
<pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">ActionSheet</span> <span class="hljs-attr">disabled</span> <span class="hljs-attr">:value</span>=<span class="hljs-string">"1"</span> <span class="hljs-attr">:data</span>=<span class="hljs-string">"[{label:'男',value:1},{label:'女',value:2}]"</span> /&gt;</span>

</code></pre>
<h4>禁用部分选项</h4>
<p>通过在<code v-pre>data</code>数据中增加disabled可禁用当前选项</p>
<h5>代码示例</h5>
<pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">ActionSheet</span> <span class="hljs-attr">:data</span>=<span class="hljs-string">"list"</span>/&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data(){
    <span class="hljs-keyword">return</span>{
      <span class="hljs-attr">list</span>: [
        { <span class="hljs-attr">label</span>: <span class="hljs-string">"选项一"</span>, <span class="hljs-attr">value</span>: <span class="hljs-number">1</span> },
        { <span class="hljs-attr">label</span>: <span class="hljs-string">"选项二"</span>, <span class="hljs-attr">value</span>: <span class="hljs-number">2</span>, <span class="hljs-attr">disabled</span>: <span class="hljs-literal">true</span> },
        { <span class="hljs-attr">label</span>: <span class="hljs-string">"选项三"</span>, <span class="hljs-attr">value</span>: <span class="hljs-number">2</span> }
      ]
    }
  }
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
<h4>异步数据</h4>
<h5>代码示例</h5>
<pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">ActionSheet</span> <span class="hljs-attr">@on-show</span>=<span class="hljs-string">"show"</span> <span class="hljs-attr">:data</span>=<span class="hljs-string">"data"</span> /&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data() {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">data</span>: []
    };
  },
  <span class="hljs-attr">methods</span>: {
    show() {
      setTimeout(<span class="hljs-function"><span class="hljs-params">()</span> =&gt;</span> {
        <span class="hljs-keyword">this</span>.data = [
          { <span class="hljs-attr">label</span>: <span class="hljs-string">"男"</span>, <span class="hljs-attr">value</span>: <span class="hljs-number">1</span> },
          { <span class="hljs-attr">label</span>: <span class="hljs-string">"女"</span>, <span class="hljs-attr">value</span>: <span class="hljs-number">2</span> }
        ];
      }, <span class="hljs-number">2000</span>);
    }
  }
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
<h4>事件</h4>
<h5>代码示例</h5>
<pre v-pre><code v-pre class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">ActionSheet</span> <span class="hljs-attr">@on-confirm</span>=<span class="hljs-string">"confirm"</span> <span class="hljs-attr">:data</span>=<span class="hljs-string">"list"</span>/&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data(){
    <span class="hljs-keyword">return</span>{
      <span class="hljs-attr">list</span>: [
        { <span class="hljs-attr">label</span>: <span class="hljs-string">"选项一"</span>, <span class="hljs-attr">value</span>: <span class="hljs-number">1</span> },
        { <span class="hljs-attr">label</span>: <span class="hljs-string">"选项二"</span>, <span class="hljs-attr">value</span>: <span class="hljs-number">2</span>, <span class="hljs-attr">disabled</span>: <span class="hljs-literal">true</span> },
        { <span class="hljs-attr">label</span>: <span class="hljs-string">"选项三"</span>, <span class="hljs-attr">value</span>: <span class="hljs-number">2</span> }
      ]
    }
  },
  <span class="hljs-attr">methods</span>: {
    confirm(value) {
      <span class="hljs-keyword">this</span>.$Toast.info(<span class="hljs-string">`选中value:<span class="hljs-subst">${value}</span>`</span>);
    }
  }
};
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
<h3>API</h3>
<h5>ActionSheet props</h5>
<table>
<thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>placeholder</td>
<td>占位内容</td>
<td>String</td>
<td>无</td>
<td>请选择</td>
</tr>
<tr>
<td>disabled</td>
<td>是否禁用</td>
<td>Boolean</td>
<td>无</td>
<td>无</td>
</tr>
<tr>
<td>close-text</td>
<td>上拉选择内容的取消内容</td>
<td>String</td>
<td>无</td>
<td>取消</td>
</tr>
<tr>
<td>label</td>
<td>data数据中在上拉菜单需要展示的字段</td>
<td>String</td>
<td>无</td>
<td>label</td>
</tr>
<tr>
<td>return-key</td>
<td>选中菜单时需要返回的字段</td>
<td>String</td>
<td>无</td>
<td>value</td>
</tr>
<tr>
<td>data</td>
<td>上拉菜展示的内容</td>
<td>Array</td>
<td>无</td>
<td>无</td>
</tr>
<tr>
<td>v-model</td>
<td>绑定的值</td>
<td>String \  Boolean \ Number</td>
<td>无</td>
<td>无</td>
</tr>
</tbody>
</table>
<h5>ActionSheet Events</h5>
<table>
<thead>
<tr>
<th>事件名</th>
<th>说明</th>
<th>回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>on-close</td>
<td>点击取消关闭上拉选择框时触发</td>
<td>无</td>
</tr>
<tr>
<td>on-mask</td>
<td>点击上拉选择框背景蒙层时触发</td>
<td>无</td>
</tr>
<tr>
<td>on-show</td>
<td>上拉选择框弹出时触发</td>
<td>无</td>
</tr>
<tr>
<td>on-confirm</td>
<td>点击上拉选择框选项时触发</td>
<td>选中的值和选中的整个对象</td>
</tr>
</tbody>
</table>
<h5>更新日志</h5>
<table>
<thead>
<tr>
<th style="text-align:center">版本</th>
<th style="text-align:left">类型</th>
<th style="text-align:left">内容</th>
</tr>
</thead>
<tbody></tbody>
</table>
</section></template>

<script>
export default {
  data(){
    return{
      list: [
        { label: "选项一", value: 1 },
        { label: "选项二", value: 2, disabled: true },
        { label: "选项三", value: 2 }
      ]
    }
  }
};
</script>


